html {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  background-image: url(./images/bgd1920.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}



